<template>
    <span @click="newer">
        {{ show_newer }}
    </span>
</template>

<script>
import user_data from '@/apis/mock/user_unit_data'
    export default {
        name: '',
        props: {
            unid: {
                type: String
            },
            is_new: {
                type: Boolean
            }
        },
        data() {
            return {
                
            }
        },
        computed: {
            show_newer() {
                if (this.is_new) { return 'yes' }
                return 'no'
            }
        },

        methods: {
            newer() {
                user_data.map(e => {
                    if (e.unid == this.unid) {
                        e.is_new = !e.is_new
                    }

                    return e
                })
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


